<template>
  <div class="container">
    <h1>Notas</h1>
    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
    >{{mensaje.texto}}</b-alert>

    <form @submit.prevent="editarNota(notaEditar)" v-if="editar">
      <h3>Editar nota</h3>
      <input type="text" class="form-control my-2" placeholder="Nombre" v-model="notaEditar.nombre" />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Descripcion"
        v-model="notaEditar.descripcion"
      />
      <b-button class="my-2 btn-warning mx-2" type="submit">Confirmar</b-button>
      <b-button class="my-2" type="submit" @click="editar = false">Cancelar</b-button>
    </form>

    <form @submit.prevent="agregarNota()" v-if="!editar">
      <h3>Agregar nueva nota</h3>
      <input type="text" class="form-control my-2" placeholder="Nombre" v-model="nota.nombre" />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Descripcion"
        v-model="nota.descripcion"
      />
      <b-button class="btn-sm btn-block btn-success" type="submit">Agregar</b-button>
    </form>

    <table class="table">
      <thead>
        <tr>
         
          <th scope="col">Nombre</th>
          <th scope="col">Descripción</th>
          <th scope="col">Fecha</th>
          <th scope="col">Acciones</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in notas" :key="index">
          
          <td>{{item.nombre}}</td>
          <td>{{item.descripcion}}</td>
          <td>{{item.date}}</td>
          <td>
            <b-button class="btn-warning btn-sm mx-2" @click="activarEdicion(item._id)">Editar</b-button>
            <b-button class="btn-danger btn-sm mx-2" @click="eliminarNota(item._id)">Eliminar</b-button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      notas: [],
      mensaje: { color: "success", texto: "" },
      dismissSecs: 5,
      dismissCountDown: 0,
      nota: { nombre: "", descripcion: "" },
      editar: false,
      notaEditar: {}
    };
  },
  created() {
    this.listarNotas();
  },
  methods: {
    listarNotas() {
      this.axios
        .get("/nota")
        .then(response => {
          // console.log(response.data)
          this.notas = response.data;
        })
        .catch(e => {
          console.log("error" + e);
        });
    },
    agregarNota() {
      this.axios
        .post("/nueva-nota", this.nota)
        .then(result => {
          this.notas.push(result.data);
          this.nota.nombre = "";
          this.nota.descripcion = "";
          this.mensaje.color = "success";
          this.mensaje.texto = "Nota agregada";
          this.showAlert();
        })
        .catch(err => {
          if (err.response.data.error.errors.nombre.message) {
            this.mensaje.texto = err.response.data.error.errors.nombre.message;
          } else {
            this.mensaje.texto = "Error al crear Nota";
          }
          this.mensaje.color = "danger";

          this.showAlert();
        });
    },
    eliminarNota(id) {
      this.axios
        .delete(`/nota/${id}`)
        .then(result => {
          this.showAlert();
          this.mensaje.texto = "Nota Eliminada!";
          this.mensaje.color = "success";
          this.listarNotas();
        })
        .catch(err => {
          console.log(err.response);
        });
    },
    activarEdicion(id) {
      this.editar = true;
      this.axios
        .get(`/nota/${id}`)
        .then(result => {
          this.notaEditar = result.data;
        })
        .catch(err => {
          console.log(err.response);
        });
    },
    editarNota(item) {
      this.axios
        .put(`/nota/${item._id}`, item)
        .then(result => {
          const index = this.notas.findIndex(n => n._id == result.data._id);
          this.notas[index].nombre = result.data.nombre;
          this.notas[index].descripcion = result.data.nombre;
          this.mensaje.texto = "Nota Actualizada!";
          this.mensaje.color = "success";
          this.showAlert();
          this.editar = false;
        })
        .catch(err => {
          console.log(err.response);
        });
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    }
  }
};
</script>
